<template>
    <div id="index">
      <div class="top">
        <img src="../assets/bg.png" alt="">
        <!--<img class="shouLine" src="../assets/title@2x.png" alt="">-->
      </div>

    <div class="box_wrap">
      <img src="../assets/input_bg@2x.png" alt="">
      <ul class="box" >
        <!--@blur="fixScroll"-->
        <li><input type="text" v-model="name" placeholder="请输入姓名" ></li>
        <li><input type="text" v-model="phone" placeholder="请输入手机号" @blur="fixScroll" ></li>

        <li class="code"><input type="text" v-model="code" placeholder="获取验证码" @blur="fixScroll" ><button :disabled="disabled" @click.stop="getCode" class="codebtn">{{codename}}</button></li>

      </ul>
    </div>
     <div class="btn" @click="submit"><img src="../assets/btn@2x.png" alt=""></div>
      <div class="bottom" >
       <!-- <img class="xf" src="../assets/bottom@2x.png" alt="">-->
        <p class="logo"><img  src="../assets/logo@2x.png"></p>
      </div>

    </div>

</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
    export default {
        name: "NewIndex",
        data(){
          return{
            name:"",
            phone:"",
            code:"",
            realCode:"",
            codename:"获取验证码",
            lists:[],
            disabled:false
          }
        },
      created(){
      this.getList()
      },
      methods:{

        fixScroll() {
          let u = navigator.userAgent;
          let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
          if (isiOS) {
            window.scrollTo(0, 0);
          }
        },
          submit(){
            this.fixScroll()

          if(this.name==''){
            alert('请输入您的姓名')
            return false;
          }
          if(this.phone==''){
            alert('请输入您的手机号')
            return false;
          }
          if(this.code!=this.realcode){
            alert('您没有输入正确的验证码，请确认后再次输入')
            return false;
          }

          var list=this.lists.find((item,index)=>{
            return this.name==item.name&&this.phone==item.phone
          })

            if(list){

              setTimeout(()=>{
                this.$router.push({path:'/gift',query: list})
              },500)

            }else {
              alert('您的信息不在本次活动中，如需确认请联系400-000-1885')
            }

          },
          getList(){
            axios.get("/hlo/static/person5.json").then((data)=>{

              this.lists=data.data
            })
          },
        getCode() {
          this.fixScroll()
          //document.getElementById("index").style.height = document.body.scrollHeight + "px";
          var myreg = /^(14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9])\d{8}$$/;
          if (this.phone == '') {
           alert('请输入您的手机号')
            return false;
          } else if (!myreg.test(this.phone)) {
            alert('手机号格式不正确，请确认后再次输入')
            return false;
          } else {
            this.disabled=true
           var backData={phone:this.phone}
            //document.getElementById("index").style.height = document.body.scrollHeight + "px";
            axios.post("https://ci.huimeicare.com/center/account/sendSmsAJAX.do",qs.stringify(backData)).then((data)=>{
              if(data.data.code==200){
                this.realcode=data.data.data
              }


            })


            var num = 60;
            var timer = setInterval(() => {
              num--;
              if (num <= 0) {
                clearInterval(timer);
                this.codename='重新发送'
                this.disabled=false
                /*this.setData({
                  codename: '重新发送',
                  disabled: false
                })*/

              } else {
                this.codename=num+"s"
                //this.disabled=false
                /*this.setData({
                  codename: num + "s",
                  disabled: true


                })*/
              }
            }, 1000)


          }
        },
      }


    }
</script>

<style scoped>
  #index{
    /*background:linear-gradient(0deg,rgba(225,79,22,1) 0%,rgba(206,0,0,1) 100%);*/
    /*position: fixed;*/
    /*top:0;*/
    /*left: 0;*/
    /*width: 100%;*/
    /*height: 100%;*/
    /*overflow: auto;*/
  }
  .top{
    position: fixed;
    top:0;
    left: 0;
  }
.top img{
  width: 100%;
}
.top .shouLine{
  position: fixed;
  top:0;
  left: 0;
  width: 5rem;

  /*margin-top: -0.8rem;*/
}
.box_wrap{
  padding-top: 6.2rem;
  margin: 0rem auto 0;
  width: 6.38rem;
  position: relative;
}
.box_wrap>img{
  position: absolute;
  width: 6.38rem;
}
.box{
  position: relative;
  padding: 0.42rem;
  /*padding-top: 0.2rem;*/
  z-index: 2;
  /*margin: 0 auto;*/
  /*width: 80%;*/
}
.box li{
 /* border: 1px solid #CC0000;*/
 margin-bottom: 0.2rem;
  border-radius: 8px;
  background: #fff;
}
.box li.code{
  display: flex;

}
.box li input{
  width: 100%;
  height: 0.9rem;
  /*line-height: 0.94rem;*/
  border-radius: 8px;
  box-sizing: border-box;
}
input{
  padding: 0.28rem 0.3rem;
  outline: none;
  -webkit-appearance: none;
 border: none;
  background: none;
  color: #333;
  font-size: 0.28rem;
}
  .btn{
    position: relative;
    z-index: 4;
    display: block;
    margin: -0.37rem auto 0;
    width: 4.08rem;


  }
.btn img{
  width: 4.08rem;
  height: auto;
}
  .codebtn{
    padding-left: 6px;
    outline: none;
    border: none;
    border-left: 1px solid #2C5687;
    margin-top: 0.2rem;
    margin-right: 0.2rem;
    width: 50%;
    background: rgba(255,255,255,.3);

    color: #2C5687;
    font-size: 0.26rem;
    height: 0.54rem;
    line-height: 0.54rem;
    text-align: center;
  }
.bottom{
  position: relative;
  /*margin-top: 1.5rem;*/
  width: 100%;
  z-index: 3;
  font-size: 0;
}
.bottom img.xf{
  width: 100%;
}
.bottom .logo{
  position: absolute;
  top:0.44rem;
  width: 100%;
  text-align: center;
  padding-bottom: 0.5rem;
}
.bottom .logo img{
  width: 2.44rem;
}
</style>



